<template>
	<div class="ywrapper">
		<ul>
			<li><img src="../../assets/images/0.jpg"></li>
			<li><img src="../../assets/images/1.jpg"></li>
			<li><img src="../../assets/images/2.jpg"></li>
			<li><img src="../../assets/images/3.jpg"></li>
			<li><img src="../../assets/images/4.jpg"></li>
			<li><img src="../../assets/images/0.jpg"></li>
		</ul>
		<div class="ybottomprev">
			<div class="ylist active"></div>
			<div class="ylist"></div>
			<div class="ylist"></div>
			<div class="ylist"></div>
			<div class="ylist"></div>
		</div>
	</div>
</template>

<script>
	export default{
		mounted(){
			let ywrapper = document.querySelector('.ywrapper');
			let ul = document.querySelector('ul');
			let img = document.querySelector('img');
			let ylist = document.querySelectorAll('.ylist');

			let index = 0;
			let timer = null;

			function move(offset) {
				ul.style.transition = '0.5s';
				ul.style.left = -parseInt(offset) * index + 'px';
			}

			function animate() {
				timer = setInterval(() => {
					setTimeout(() =>{
						next.onclick()
					},1000)
				},3000)
			}

			ywrapper.addEventListener('touchstart',function(e){
				clearInterval(timer)
				clickButton()
			})


			ywrapper.addEventListener('touchmove',function(e){
				moveX
			})

			ywrapper.onmouseout = function() {
				animate()
			}

			function classButton() {
				for(let i = 0;i < ylist.length; i++){
					ylist[i].classList.remove('active')
				}
				if (index == ylist.length) {
					ylist[0].classList.add('active')
				}
				ylist[index].classList.add('active')
			}

			function clickButton() {
				for(let i = 0;i < ylist.length; i++){
					ylist[i].onclick = function() {
						index = i;
						move(img.offsetWidth);
						classButton()
					}
				}
			}

			clickButton()

		}
	}
</script>

<style lang="scss" scoped>
	.ywrapper{
		width: 6rem;
		height: 3.38rem;
		overflow: hidden;	
		margin: 0 auto;
		position: relative;
		ul{
			display: flex;
			position:absolute;
			left: 0;
			li{
				img{
					width: 6rem;

				}
			}
		}
		.ybottomprev{
			width: 100%;
			height: .08rem;
			position:absolute;
			display: flex;
			bottom:.16rem;
			justify-content: center;
			.ylist{
				width: .7rem;
				height: .08rem;
				display: inline-block;
				background: rgba(0,0,0,.6);
				margin: .06rem;
				&.active{
					background:white;
				}
			}
		}
	}
</style>